/* reset css */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a{
	text-decoration: none;
}
/* rwd img css，可當作是 CSS Reset 其中一個 */

img{
  max-width: 100%;
  height: auto;
}
.d-f{
    display: flex;
}

/* 全域 border box */

*,*::before,*::after{
  box-sizing: border-box;
}
.container {
    max-width: 1140px;
    margin: 0 auto;
    padding: 15 auto;
    line-height: 1.5;
}
@media (max-width:768px) {
    .container{
        max-width: 720px;
    }
}
@media (max-width:767px){
    .container{
        max-width: 720px;
    }
}

/* header */
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #AA0601;
    padding: 0 56px 0 88px;
}
@media (max-width:780px){
    .header{
        padding: 0 7px 0 39px;
    }
}
@media (max-width:767px){
    .header{
        flex-direction: column;
        padding: 0;
    }
}
.logo a{
    display: block;
    width: 80px;
    height: 49px;
    margin: 30px 0 ;
    background:url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/logo.png) center center no-repeat;
    background-size: cover;
    text-indent: 101%;
    white-space: nowrap;
    overflow: hidden;
}
@media (max-width:767px){
    .logo a {
        width: 48px;
        height: 29px;
        margin: 10px 0;
    }
}
.menu{
    display: flex;   
}
@media (max-width:767px){
    .menu{
        flex-wrap: wrap;
    }
}
.menu a{
    display: block;
    color: #ffffff;
    font-size: 20px;
    padding: 39px 32px;
}
.menu a:hover{
    color: rgb(36, 196, 22);
}
@media (max-width:768px){
    .menu a{ padding: 39px 20px; }
}
@media (max-width:767px){  
    .menu li {
        width: 50%;
        border: #ffffff 1px solid;
        text-align: center;
        padding: 8px 52px;
    }
    .menu a{
        display: block;
        padding: 0;
    }
   
}
/* faq */
.faq{
    padding-top: 96px;
    padding-bottom: 128px;
}
.faq h2{
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 60px;
}
.faq h2::before{
    content: "";
    border-right: 8px solid #9F1213;
    margin-right: 16px;
}
.faq h3{
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 36px;
}
.faq h4{
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 16px;
}
.faq p{
    margin-left: 95px;
    margin-bottom: 32px;
}
@media (max-width:768px)
{
    .faq{
        padding-top: 60px;
        padding-bottom: 72px;
    }
    .faq h2{
        margin-bottom: 40px;
    }
    .faq h2::before{
        border-right: 4px solid #9F1213;}
    .faq h3{
        margin-bottom: 32px;
        }
    .faq p{margin-left: 59px;
           margin-bottom: 24px;}
}
@media (max-width:767px){
    .faq{   
        padding-top: 28px;
        margin-bottom: 36px;    
    }
    .faq h2{
        font-size: 28px;
        margin-bottom: 24px;
    }
    .faq h2::before{
        margin-right: 8px;
        }
    .faq h3{
        font-size: 20px;
        font-weight: normal;
        margin-bottom: 24px;
    }
    .faq h4{
        font-size: 16px;
        margin-bottom: 8px;
    }
    .faq p{
        font-size: 14px;
        margin-bottom: 20px;
    }

}
